h2 {
    width: 100%;
}

.title {
    margin: 5px;
    width: 100%;
}

.content {
    /*margin: 5px;*/
    width: 100%;
    height: 900px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.post {
    flex: 20%;
    padding: 10px;
    margin: 5px;
    background-color: #f3f3f3;
    inline-size: 100%;
}

.title a {
    color: #00a36f;
    text-decoration: none;
}

nav a {
    color: #d64161;
    font-size: 3em;
    margin-left: 50px;
    text-decoration: none;
}

.flex-container {
  display: flex;
  justify-content: space-between;
}

.flex-item-left {
  align-self: flex-start;
}

.flex-item-right {
  align-self: flex-end;
}